import {createActions} from 'redux-actions';

const {
  receiveUserInfo,
  receiveNewChatMessage,
  receiveLastMessages,
  receiveUserStatus,
  receiveAccessToken,
  loginError,
} = createActions(
  'RECEIVE_USER_INFO',
  'RECEIVE_NEW_CHAT_MESSAGE',
  'RECEIVE_LAST_MESSAGES',
  'RECEIVE_USER_STATUS',
  'RECEIVE_ACCESS_TOKEN,',
  'LOGIN_ERROR',
);

export {
  receiveUserInfo,
  receiveNewChatMessage,
  receiveLastMessages,
  receiveUserStatus,
  receiveAccessToken,
  loginError,
}



