import {createActions} from 'redux-actions';

export const {
  connectSocket,
  disconnectSocket,
  logout,
} = createActions(
  {
    'CONNECT_SOCKET': [
      ({name, socket}) => ({name, socket}),
      ({name}) => ({requireAuth: name == 'common' ? false : true})
    ]
  },
  'DISCONNECT_SOCKET',
  'LOGOUT',
);



