import {combineReducers} from 'redux';
import {handleAction, handleActions} from 'redux-actions';

import * as actions from 'actions';

const {connectSocket, disconnectSocket} = actions;
const sockets = handleActions({
  [connectSocket]: (state, {payload: {name, socket}}) => {
    return {...state, [name]: socket};
  },
  [disconnectSocket]: (state, {payload: name}) => {
    delete state[name];
    return {...state};
  }
}, {});

const token = {accessToken: localStorage.getItem('accessToken')};
const {receiveAccessToken, logout} = actions;
const authorization = handleActions({
  [receiveAccessToken]: (state, {payload}) => {
    const {accessToken} = payload;
    localStorage.setItem('accessToken', accessToken);
    return {...state, ...payload};
  },
  [logout]: state => {
    delete state.accessToken;
    localStorage.removeItem('accessToken');
    return {...state};
  },
  UNAUTHORIZED: state => {
    delete state.accessToken;
    return state;
  }
}, {...token});

const {receiveUserInfo} = actions;
const user = handleAction(receiveUserInfo, (state, {payload}) => payload.self ? payload : state, {});

const {receiveNewChatMessage, receiveLastMessages} = actions;
const messages = handleActions({
  [receiveNewChatMessage]: (state, {payload}) => [...state, payload],
  [receiveLastMessages]: (state, {payload}) => {
    console.log(payload);
    return [...state, ...payload];
  }
}, []);

const {receiveUserStatus} = actions;
const users = handleActions({
  [receiveUserInfo]: (state, {payload}) => payload.self ? state : {...state, [payload.id]: payload},
  [receiveUserStatus]: (state, {payload}) => state
}, {});

export default combineReducers({
  sockets,
  authorization,
  user,
  chat: combineReducers({
    users,
    messages
  })
});



