import React from 'react';
import {Grid, Row, Col, Panel} from 'react-bootstrap';

const NotFound = props => <Grid fluid>
  <Row>
    <Col>
      <Panel>
        <h1>404 Not Found</h1>
      </Panel>
    </Col>
  </Row>
</Grid>;

export default NotFound;



