import {Component} from 'react';
import {connect} from 'react-redux';
import {Grid, Row, Col} from 'react-bootstrap';
import io from 'socket.io-client';
import {host} from 'config';
import {connectSocket, disconnectSocket, getUserInfo, newMessage, getLastMessages} from 'actions';
import scrollIntoView from 'scroll-into-view';

const defaultUserpic = '/userpic.jpg';

class Chat extends Component {
  state = {
    message: ''
  };
  handleChange = (e) => {
    const {target: {name, value}} = e;
    if (name) this.setState({[name]: value});
  }
  sendMessage = e => {
    e.preventDefault();
    this.props.newMessage(this.state.message);
    this.setState({message: ''});
  };
  componentWillMount() {
    const socket = io(`${host}/chat`, {path: '/api'});
    this.props.connectSocket({name: 'chat', requireAuth: true, socket});
  }
  componentWillUnMount() {
    this.props.disconnectSocket('chat');
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.chat && !this.state.inited) {
      this.props.getLastMessages();
      this.setState({inited: true});
    }
  }
  componentDidUpdate() {
    scrollIntoView(this.refs.input);
  }
  render() {
    const {user, users, messages} = this.props;
    return <Grid fluid className='chat'>
      <Row>
        <Col sm={3} xs={12}>
          <div className='col-inside-lg decor-default chat' style={{overflow: 'hidden', outline: 'none'}} tabIndex='5000'>
            <div className='chat-users'>
              <h6>Users</h6>
              {Object.keys(users).map((e, i) => {
                const {img, name, status} = users[e];
                return <div key ={i} className='user'>
                  <div className='avatar'>
                    <img src={img || defaultUserpic} alt='User name' />
                    <div className={`status ${status}`}></div>
                  </div>
                  <div className='name'>{name}</div>
                </div>;
              }
            )}
            </div>
          </div>
        </Col>
        <Col sm={9} xs={12}>
          <div className='col-inside-lg decor-default' style={{height: '80vh', 'overflow-y': 'scroll'}}>
            <div className='chat-body'>
              <h6>Chat</h6>
              {messages.map((e, i) => {
                const self = e.userId == user.id;
                let userinfo = self ? user : users[e.userId];
                if (!userinfo) {
                  this.props.getUserInfo(e.userId);
                  userinfo = {
                    name: 'unknown',
                    img: defaultUserpic,
                    status: 'unknown'
                  };
                }
                let {name, img, status} = userinfo;
                if (!img) img = defaultUserpic;
                const answer = self ? 'right' : 'left';
                const {text, time} = e;
                return <div key={i} className={`answer ${answer}`}>
                  <div className='avatar'>
                    <img src={img} alt='User name' />
                    <div className={`status ${status}`}></div>
                  </div>
                  <div className='name'>{name}</div>
                  <div className='text'>{text}</div>
                  <div className='time'>{new Date(time).toString()}</div>
                </div>;
              })}
              <div className='answer-add'>
                <input placeholder='Write a message' name='message' onKeyPress={e => {
                  if (e.key == 'Enter') this.sendMessage(e);
                }} onChange={this.handleChange} value={this.state.message} ref='input' />
                {/* <span className='answer-btn answer-btn-1'></span> */}
                <span className='answer-btn answer-btn-2' onClick={this.sendMessage}></span>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </Grid>;
  }
}

export default connect(
  ({chat: {messages, users}, sockets: {chat}, user}) => ({chat, messages, users, user}),
  {connectSocket, disconnectSocket, getUserInfo, newMessage, getLastMessages}
)(Chat);



